export const paramsModule = {

}

export const baseMinxis = {
  data() {
    return {
      tableColumn: [
        {
          title: '流水时间',
          field: 'goods_id',
          formatter: ({ row }) => {
            return this.$publicFution.getAllTime(row.lg_add_time * 1000)
          },
          width: 160
        },
        {
          title: '收入（元）',
          field: 'goods_sn',
          headerAlign: "right",
          align: "right",
          formatter: ({ row }) => {
            return row.lg_av_amount >= 0 ? row.lg_av_amount : ''
          },
          width: 160
        },
        {
          title: '支出（元）',
          field: 'lg_av_amount',
          headerAlign: "right",
          align: "right",
          formatter: ({ row }) => {
            return row.lg_av_amount <= 0 ? row.lg_av_amount : ''
          },
          width: 160
        },
        {
          title: '冻结（元）',
          field: 'lg_freeze_amount',
          headerAlign: "right",
          align: "right",
          width: 160
        },
        {
          title: '变更说明',
          field: 'lg_desc',
        },
      ]
    }
  }
}